.navbar {
  width: 100%;
  position: fixed;
  background-color: #000000;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  
  top: 0;
  z-index: 1000;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  max-width: 1500px;
}

.main-container{
  position:absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  background-color:rgba(0,0,0,0.3);
}

.nav-logo {
  color: #ffffff;
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  margin-bottom: 15px;
  padding-bottom: 6px;
  flex-grow: 1;
}
.nav-logo .logo{
width: 5vmax;
padding-top: 1vmax;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: rgb(255, 255, 255);
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
}
.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

/* Add these styles to your existing CSS file (nav.css) */

/* Styling for the dropdown menu */


/* Styling for the arrow symbol */

/* ------------------------------------------------------------------  */
.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: orangered;
}

.nav-item.active {
  color: #fbde5e;
  border: 1px solid #ffdd40;
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {
 
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height:100vmin;
    border-top: 1pxsolid #fff;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.7s ease;
  }

  .nav-menu.active {
    background: #000000;
    left: 0;
    height: 100vh;
    opacity: 1;
    transition: all 0.7s smooth;
    z-index: 1;
  }
  .nav-item .active {
    color: #ffdd40;
    border: none;
    left:0;
  }
  .nav-links {
   margin-bottom: 2rem;
    width: 100%;
    display: table;
    left:0;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #ffdd40;
  }
  .nav-item:hover:after {
    width: 100%;
    background: rgba(255, 68, 0, 0);
  }
}


/* Position fixed to the viewport  ------------------------------------------------------------------------------*/
.speedDial {
  position: fixed; /* fixed position */
  right: 3vmax;    /* right position */
  top: 6vmax;      /* top position */
 }

.speedDialIcon {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  border-style:groove;
  border-width: 4px;
  
  border-bottom-color: rgb(0, 0, 0);
  border-left-color: rgb(0, 0, 0);
  border-right-color: rgb(0, 0, 0);
  border-top-color: rgb(0, 0, 0);

}